<template>
  <div class="evaluate">
    <div class="score-box">
      <ul class="stars">
        <li class="item" v-for="item in score">
        </li>
      </ul>
      <div class="score">
        <span>5.0</span>
        <span>分</span>
      </div>
      <p class="describe">非常满意，无可挑剔</p>
    </div>
    <ul class="contents">
      <li class="item clearfix" v-for="item in msg">
        <router-link class="pic" to="/user-data">
          <img v-lazy="item.pic" alt="">
        </router-link>
        <div class="eva-data">
          <p class="name">{{item.name}}</p>
          <p class="eva-content">{{item.eva_content}}</p>
        </div>
        <span class="time">{{item.time}}</span>
        <span class="date">{{item.date}}</span>
      </li>
    </ul>
  </div>
</template>

<script>
  import "@/assets/style/home-page/evaluate.scss";
  export default{
    name: "Evaluate",
    data(){
      return {
        score: [1, 2, 3, 4, 5],
        msg: [
          {
            id: 1,
            name: "狮子",
            pic: "/static/images/home-page/pic_.png",
            eva_content: "非常满意",
            date: "2017-11-11",
            time: "14:11"
          },
          {
            id: 2,
            name: "狮子",
            pic: "/static/images/home-page/pic_.png",
            eva_content: "非常满意",
            date: "2017-11-11",
            time: "14:11"
          },
          {
            id: 3,
            name: "狮子",
            pic: "/static/images/home-page/pic_.png",
            eva_content: "非常满意",
            date: "2017-11-11",
            time: "14:11"
          },
        ]
      }
    }
  }
</script>



