<template>
  <div class="my-orders">
    <header>
      <div class="return" @click="goBack()"></div>
      <p>我的订单</p>
    </header>
    <ul class="nav-bar">
      <li class="item" v-for="item in msg_nav" :class="{'active':active===item.id}" @click="setActive(item.id)">
        {{item.name}}
      </li>
    </ul>
  </div>
</template>

<script>
  import router from "@/router/index";
  import "../../assets/style/personal-center/my-orders.scss";
  export default{
    name: "MyOrders",
    data(){
      return {
        active: 1,
        msg_nav: [
          {id: 1, name: "未付款"},
          {id: 2, name: "待发货"},
          {id: 3, name: "未收货"},
          {id: 4, name: "未评价"},
          {id: 5, name: "已完成"}
        ]
      }
    },
    methods: {
      goBack: function () {
        router.go(-1);
      },
      setActive: function (id) {
        this.active = id;
      }
    }
  }
</script>



