<template>
  <div class="search">
    <header>
      <div class="return" @click="goBack()"></div>
      <div class="search-box">
        <div class="icon"></div>
        <input class="pingfang-R" type="text" placeholder="搜索科目/机构/课程">
      </div>
    </header>
    <ul class="classify">
      <li class="item">
        <div class="icon"></div>
        <p>科目</p>
        <router-link class="mask" to="/contents-list/subjects-list"></router-link>
      </li>
      <li class="item">
        <div class="icon"></div>
        <p>机构</p>
        <router-link class="mask" to="/contents-list/institutions-list"></router-link>
      </li>
      <li class="item">
        <div class="icon"></div>
        <p>课程</p>
        <router-link class="mask" to="/contents-list/courses-list"></router-link>
      </li>
    </ul>
  </div>
</template>

<script>
  import router from "../../router/index"
  import "@/assets/style/home-page/search.scss";
  export default{
    name: "Search",
    data(){
      return {}
    },
    methods: {
      goBack: function () {
        router.go(-1);
      },
      search: function () {
        if ($(".search header input").val()) {
          router.push("/contents-list/subjects-list");
        } else {
          console.log("none");
        }
      }
    },
    mounted: function () {
      $(".search header input").keydown(function (e) {
        if (e.keyCode === 13) {
          $(".search header input").blur();
          this.search()
        }
      }.bind(this))
    }
  }
</script>



