<template>
  <div class="personal-center">
    <header>
      <div class="pic">
        <img :src="msg.pic" alt="">
        <div class="grade" v-show="msg.status"></div>
        <router-link class="mask" :to="msg.status?'':'/login'"></router-link>
      </div>
      <div class="pic-bottom">
        <p class="name">{{msg.name}}</p>
        <router-link to="/personal-data" class="personalData-button" v-show="msg.status"></router-link>
      </div>
      <ul class="courses-orders">
        <li class="item">
          <div class="icon"></div>
          <p>我的课程</p>
          <router-link :to="msg.status?'/my-courses':'/login'" class="mask"></router-link>
        </li>
        <li class="item">
          <div class="icon"></div>
          <p>我的订单</p>
          <router-link :to="msg.status?'/my-orders':'/login'" class="mask"></router-link>
        </li>
      </ul>
    </header>
    <ul class="content">
      <li class="item clearfix" v-for="item in content">
        <img :src="item.pic" alt="" class="icon">
        <span>{{item.name}}</span>
        <router-link :to="msg.status?item.path:'/login'" class="come-in"></router-link>
      </li>
    </ul>
    <div class="quit-button" v-show="msg.status" @click="setStatus()">退出登录</div>
    <TabBar></TabBar>
  </div>
</template>

<script>
  import "../../assets/style/personal-center/personal-center.scss"
  import TabBar from "@/components/func-components/TabBar";
  export default {
    name: 'PersonalCenter',
    components: {
      TabBar
    },
    data () {
      return {
        msg: {
          id: 0,
          name: "未登录",
          status: false,
          pic: "/static/images/personal-center/pic_none.png",
        },
        content: [
          {id: 1, pic: "/static/images/personal-center/kids_archive.png", name: "孩子档案", path: "/kids-archive-list"},
          {id: 2, pic: "/static/images/personal-center/collections.png", name: "我的收藏", path: "/my-collections"},
          {id: 3, pic: "/static/images/personal-center/comments.png", name: "我的评价", path: "/my-comments"},
          {id: 4, pic: "/static/images/personal-center/addresses.png", name: "地址管理", path: "/my-addresses"},
          {id: 5, pic: "/static/images/personal-center/feedback.png", name: "我要反馈", path: "/feedback"}
        ]
      }
    },
    methods: {
      setStatus: function () {
        this.msg = {
          id: 0,
          name: "未登录",
          status: false,
          pic: "/static/images/personal-center/pic_none.png",
        }
      }
    },
    created: function () {
      this.msg = {
        id: 122,
        name: "王大剑",
        status: true,
        pic: "/static/images/personal-center/my_pic.png",
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



