<template>
  <div class="register">
    <header>
      <div class="return" @click="goBack()"></div>
      <p>新用户注册</p>
    </header>
    <ul class="content">
      <li class="item">
        <input type="text" class="phone" placeholder="请输入您的手机号" @blur="check(1)">
        <p class="false-hint" v-show="false_input.phone">{{false_input.phone === 1 ? "手机号码不能为空" : "请填写正确的手机号码"}}</p>
      </li>
      <li class="item">
        <input type="text" class="code" placeholder="请输入您的短信验证码">
        <div class="send-button" @click="sendCode()">发送验证码</div>
        <p class="false-hint" v-show="false_input.code">请填写验证码</p>
      </li>
      <li class="item">
        <input type="password" class="password" placeholder="请设置8-16位的密码" @blur="check(3)">
        <p class="false-hint" v-show="false_input.password">{{false_input.password === 1 ? "密码不能为空" : "密码格式错误"}}</p>
      </li>
      <li class="item">
        <input type="password" class="password-again" placeholder="请确认密码" @blur="check(4)">
        <p class="false-hint" v-show="false_input.password_again">两次输入不一致</p>
      </li>
    </ul>
    <div class="submit" @click="submit()">完成</div>
  </div>
</template>

<script>
  import router from "@/router/index";
  import "../../assets/style/personal-center/register.scss";
  export default{
    name: "Register",
    data(){
      return {
        false_input: {
          phone: false,
          code: false,
          password: false,
          password_again: false
        }
      }
    },
    methods: {
      goBack: function () {
        router.go(-1);
      },
      sendCode: function () {
        var phone_num = $(".register .content input.phone").val();
        if (this.false_input.phone) {
          console.log("false");
        } else {
          if (phone_num) {
            console.log("success");
          } else {
            this.false_input.phone = 1;
          }
        }
      },
      check: function (para) {
        var reg_phone = /^1[3|4|5|7|8][0-9]{9}$/;
        var reg_password = /^[A-Za-z0-9._]{8,16}$/;
        //        var reg=/^(\w){8,16}$/;
        var phone_num = $(".register .content input.phone").val();
        var password = $(".register .content input.password").val();
        var password_again = $(".register .content input.password-again").val();
        if (para === 1) {
          if (phone_num) {
            if (reg_phone.test(phone_num)) {
              this.false_input.phone = false;
            } else {
              this.false_input.phone = true;
            }
          } else {
            this.false_input.phone = false;
          }
        }
        if (para === 3) {
          if (password) {
            if (reg_password.test(password)) {
              this.false_input.password = false;
            } else {
              this.false_input.password = true;
            }
          } else {
            this.false_input.password = false;
          }
        }
        if (para === 4) {
          if (password_again) {
            if (password_again === password) {
              this.false_input.password_again = false;
            } else {
              this.false_input.password_again = true;
            }
          } else {
            this.false_input.password_again = false;
          }
        }
      },
      submit: function () {
        var phone_num = $(".register .content input.phone").val();
        var password = $(".register .content input.password").val();
        var password_again = $(".register .content input.password-again").val();
        if (this.false_input.phone) {
          if (this.false_input.password) {
            if (this.false_input.password) {

            }
          } else {
          }
        } else {
          if (!phone_num) {
          }
        }
      }
    }
  }
</script>



