<template>
  <div class="location-page">
    <header>
      <div class="return" @click="goBack()"></div>
      <div class="search">
        <div class="icon"></div>
        <input class="pingfang-R" type="text" placeholder="输入城市名称或者拼音查询">
      </div>
    </header>
    <ul class="city-location">
      <li class="item">
        <div class="icon"></div>
        <span>当前城市</span>
        <div class="name">杭州</div>
      </li>
      <li class="item">
        <div class="icon"></div>
        <span>定位城市</span>
        <div class="name">上海</div>
        <span class="relocation">重新定位</span>
      </li>
    </ul>
    <ul class="city-list">
      <li class="list-item">
        <p class="item-title">最近访问城市</p>
        <ul class="contents">
          <li class="item" v-for="item in msg.recent_visit">{{item}}</li>
        </ul>
      </li>
      <li class="list-item">
        <p class="item-title">国内热门城市</p>
        <ul class="contents">
          <li class="item" v-for="item in msg.hot_city">{{item}}</li>
        </ul>
      </li>
    </ul>
  </div>
</template>

<script>
  import router from "../../router/index"
  import "@/assets/style/home-page/location-page.scss";
  export default{
    name: "LocationPage",
    data(){
      return {
        msg: {
          current_city: "杭州",
          loc_city: "上海",
          recent_visit: ['杭州', '澳门', '北京'],
          hot_city: ['杭州', '澳门', '北京', '杭州', '澳门', '北京', '杭州', '澳门', '北京', '杭州', '澳门', '北京', '杭州', '澳门', '北京']
        }
      }
    },
    methods: {
      goBack: function () {
        router.go(-1);
      }
    }
  }
</script>



