<template>
  <div class="login">
    <div class="logo"></div>
    <div class="login-box">
      <input type="text" placeholder="请输入您的手机号">
      <input type="password" placeholder="请输入您的密码">
      <router-link to="/login-by-code" class="login-no-password">手机验证码登录</router-link>
    </div>
    <ul class="button-box">
      <li class="item">
        <span>登</span>
        <span>陆</span>
        <router-link class="mask" to="/personal-center"></router-link>
      </li>
      <li class="item">
        <span>注</span>
        <span>册</span>
        <router-link class="mask" to="/register"></router-link>
      </li>
    </ul>
    <footer>
      <div class="title">
        <div class="line"></div>
        <span>其他登陆</span>
        <div class="line"></div>
      </div>
      <ul class="methods">
        <li class="method">
          <div class="icon"></div>
          <p>支付宝</p>
          <router-link class="mask" to="/login-by-weChat"></router-link>
        </li>
        <li class="method">
          <div class="icon"></div>
          <p>微博</p>
          <router-link class="mask" to="/login-by-weChat"></router-link>
        </li>
        <li class="method">
          <div class="icon"></div>
          <p>微信</p>
          <router-link class="mask" to="/login-by-weChat"></router-link>
        </li>
      </ul>
    </footer>
  </div>
</template>

<script>
  import "../../assets/style/personal-center/login.scss";
  export default{
    name: "Login",
    data(){
      return {}
    }
  }
</script>



