<template>
  <div class="login-by-code">
    <header>
      <div class="return" @click="goBack()"></div>
      <p>验证码登录</p>
    </header>
    <div class="content">
      <input class="phone" type="text" :placeholder="placeholder" @focus="setPlaceholder('')"
             @blur="setPlaceholder('请输入手机号码')">
      <p class="hint">我们会向该号码发送短信验证码</p>
      <ul class="identify-code" v-show="status">
        <li class="item">
          <input type="text" @focus="codeRemove(0)" @input="codeInput(0)">
        </li>
        <li class="item">
          <input type="text" @focus="codeRemove(1)" @input="codeInput(1)">
        </li>
        <li class="item">
          <input type="text" @focus="codeRemove(2)" @input="codeInput(2)">
        </li>
        <li class="item">
          <input type="text" @focus="codeRemove(3)" @input="codeInput(3)">
        </li>
        <li class="item">
          <input type="text" @focus="codeRemove(4)" @input="codeInput(4)">
        </li>
        <li class="item">
          <input type="text" @focus="codeRemove(5)" @input="codeInput(5)">
        </li>
      </ul>
      <p class="hint" v-show="status">请填写 {{msg.phone}} 收到的短信验证码</p>
      <div class="agreement-button">
        <div class="pick-button" :class="{'active':agreement}" @click="setAgreement()"></div>
        <span>我已阅读并同意</span>
        <router-link to="/agreement">《淘儿学用户服务协议》</router-link>
      </div>
      <p class="hint false-hint">{{false_hint}}</p>
    </div>
    <div class="submit-code" @click="submit()">{{status ? '登录' : '发送验证码'}}</div>
  </div>
</template>

<script>
  import router from "@/router/index";
  import "../../assets/style/personal-center/login-by-code.scss";
  export default{
    name: 'LoginByCode',
    data(){
      return {
        status: false,
        placeholder: "请输入手机号码",
        agreement: false,
        false_hint: "",
        msg: {
          phone: "",
          identify_code: ""
        }
      }
    },
    methods: {
      goBack: function () {
        router.go(-1);
      },
      setAgreement: function () {
        this.agreement = this.status ? true : !this.agreement;
      },
      setPlaceholder: function (para) {
        this.placeholder = para;
      },
      submit: function () {
        if (!this.status) {
          var reg = /^1[3|4|5|7|8][0-9]{9}$/;
          var phoneNumber = $(".login-by-code .content input.phone").val();
          if (reg.test(phoneNumber)) {
            if (this.agreement) {
              this.msg.phone = phoneNumber;
              this.status = true;
            } else {
              this.false_hint = "请阅读并勾选用户服务协议";
              setTimeout(function () {
                this.false_hint = "";
              }.bind(this), 2000)
            }
          } else {
            this.false_hint = "请输入正确的手机号码";
            setTimeout(function () {
              this.false_hint = "";
            }.bind(this), 2000)
          }
        } else {
          console.log(this.msg);
        }
      },
      codeRemove: function (index) {
        $(".login-by-code .content .identify-code li.item input")[index].value = "";
      },
      codeInput: function (index) {
        var value = $(".login-by-code .content .identify-code li.item input")[index].value;
        if (Number(value) || Number(value) === 0) {
          this.msg.identify_code += value;
          if (index < 5) {
            $(".login-by-code .content .identify-code li.item input")[index + 1].focus();
          } else {
            $(".login-by-code .content .identify-code li.item input")[index].blur();
          }
        } else {
          $(".login-by-code .content .identify-code li.item input")[index].value = "";
        }
      }
    }
  }
</script>



