<template>
  <div class="my-comments">
    <header>
      <div class="return" @click="goBack()"></div>
      <p>我的评价</p>
    </header>
  </div>
</template>

<script>
  import router from "@/router/index";
  import "../../assets/style/personal-center/my-comments.scss";
  export default{
    name: "MyComments",
    data(){
      return {
        msg: []
      }
    },
    methods: {
      goBack: function () {
        router.go(-1);
      }
    }
  }
</script>



