            <span>{{item.class_hour}}</span>
            <span>节课</span>
          </li>
        </ul>
        <div class="price clearfix">
          <span>￥</span>
          <span>{{item.price}}</span>
        </div>
      </div>
      <div class="course-achievement">
        <div class="icon"></div>
        <span class="beauty">美少女</span>
        <div class="score">
