            id: 2,
            institution: "棒棒的音乐课",
            pic: "/static/images/home-page/course.jpg",
            subject: "音乐",
            class_hour: 3,
            price: "360.00",
            describe: "很棒很棒很棒很棒很棒很棒很棒很棒很棒很棒的音乐课",
            score: "5.0",
            students_num: 123
          },
          {
            id: 3,
            institution: "棒棒的音乐课",
            pic: "/static/images/home-page/course.jpg",
            subject: "音乐",
            class_hour: 3,
            price: "360.00",
            describe: "很棒很棒很棒很棒很棒很棒很棒很棒很棒很棒的音乐课",
            score: "5.0",
            students_num: 123
          }
        ]
      }
    }
  }
</script>



