<template>
  <div class="course-category">
    <header>
      <div class="return" @click="goBack()"></div>
      <div class="search">
        <div class="icon"></div>
        <input class="pingfang-R" type="text" placeholder="搜索科目/机构/课程">
      </div>
    </header>
    <ul class="content">
      <li class="item" v-for="item in msg">
        <img :src="item.pic" alt="" class="pic">
        <p class="name">{{item.name}}</p>
        <router-link class="mask" :to="item.path"></router-link>
      </li>
    </ul>
  </div>
</template>

<script>
  import router from "../../router/index"
  import "@/assets/style/home-page/course-category.scss";
  export default{
    name: "CourseCategory",
    data(){
      return {
        msg: [
          {id: 1, pic: "/static/images/home-page/subject01.png", name: "英语",path:"/contents-list/subjects-list"},
          {id: 2, pic: "/static/images/home-page/subject02.png", name: "数学",path:"/contents-list/subjects-list"},
          {id: 3, pic: "/static/images/home-page/subject03.png", name: "语文",path:"/contents-list/subjects-list"},
          {id: 4, pic: "/static/images/home-page/subject04.png", name: "体育",path:"/contents-list/subjects-list"},
          {id: 5, pic: "/static/images/home-page/subject05.png", name: "美术",path:"/contents-list/subjects-list"},
          {id: 6, pic: "/static/images/home-page/subject06.png", name: "舞蹈",path:"/contents-list/subjects-list"},
          {id: 7, pic: "/static/images/home-page/subject07.png", name: "音乐",path:"/contents-list/subjects-list"},
          {id: 8, pic: "/static/images/home-page/subject08.png", name: "国学",path:"/contents-list/subjects-list"},
          {id: 9, pic: "/static/images/home-page/subject10.png", name: "益智",path:"/contents-list/subjects-list"}
        ]
      }
    },
    methods: {
      goBack: function () {
        router.push("/");
      }
    }
  }
</script>



