<template>
  <div class="course-catalog-UL">
    <div class="title">
      <span>共</span>
      <span>{{msg.section_num}}</span>
      <span>节课</span>
    </div>
    <ul class="contents">
      <li class="item clearfix" v-for="item in msg.details">
        <div class="index">{{item.id}}</div>
        <div class="details">
          <p class="section-title">{{item.title}}</p>
          <div class="span-box">
            <span class="date">{{item.date}}</span>
            <span>{{item.date_}}</span>
            <span class="time">{{item.time}}</span>
          </div>
        </div>
      </li>

    </ul>
  </div>
</template>

<script>
  import "@/assets/style/home-page/course-catalog-UL.scss";
  export default{
    name: "CourseCatalogUL",
    data(){
      return {
        msg: {
          section_num: 3,
          details:[
            {id:1,title:"小羊的故事",date:"2017-11-15",time:"19：30-20：30",date_:"周三"},
            {id:2,title:"小羊的故事",date:"2017-11-15",time:"19：30-20：30",date_:"周三"},
            {id:3,title:"小羊的故事",date:"2017-11-15",time:"19：30-20：30",date_:"周三"}
          ]
        }
      }
    }
  }
</script>



