<template>
  <div class="kid-data">
    <header>
      <div class="return" @click="goBack()"></div>
      <p>个人资料</p>
    </header>
    <ul class="content">
      <li class="item">
        <span class="item-name">头像</span>
        <div class="item-content">
          <img :src="msg.pic" alt="" class="pic">
          <div class="come-in"></div>
          <div class="mask" @click="setStatus(true)"></div>
        </div>
      </li>
      <li class="item">
        <span class="item-name">姓名</span>
        <div class="item-content">
          <span>{{msg.name}}</span>
        </div>
      </li>
      <li class="item">
        <span class="item-name">性别</span>
        <div class="item-content">
          <span>{{msg.gender}}</span>
        </div>
      </li>
      <li class="item">
        <span class="item-name">生日</span>
        <div class="item-content">
          <span>{{msg.birthday}}</span>
        </div>
      </li>
      <li class="item">
        <span class="item-name">与宝宝的关系</span>
        <div class="item-content">
          <span>{{msg.relationship}}</span>
        </div>
      </li>
    </ul>
    <div class="pic-set" v-show="status">
      <div class="shadow" @click="setStatus(false)"></div>
      <div class="section-bottom">
        <p>拍照</p>
        <p>从相册选择</p>
        <p @click="setStatus(false)">取消</p>
      </div>
    </div>
  </div>
</template>

<script>
  import router from "@/router/index";
  import "../../assets/style/personal-center/kid-data.scss";
  export default{
    data(){
      return {
        msg: {
          id: 122,
          name: "王大剑",
          gender: "男",
          birthday: "11-11",
          relationship: "妈妈",
          status: true,
          pic: "/static/images/personal-center/my_pic.png",
        },
        status: false
      }
    },
    methods: {
      goBack: function () {
        router.go(-1);
      },
      setStatus: function (para) {
        this.status = para;
      }
    }
  }
</script>



